@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mochiy+Pop+P+One&display=swap);
body {
  margin: 0;
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box}body{background-color:#f0f0f0;color:#2b2b2b;font-family:"Roboto",sans-serif}a{text-decoration:none;color:unset}
.horizontal-bar-left{background-color:red;flex:1 1}
.cell{background-color:red;flex:1 1}
label{position:relative;display:inline-block;width:60px;height:30px}input{opacity:0;width:0;height:0}span{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background:#2c3e50;transition:.3s;border-radius:30px}span:before{position:absolute;content:"";height:25px;width:25px;left:3px;bottom:2.6px;background-color:#fff;border-radius:50%;transition:.3s}input:checked+span{background-color:#00c853}input:checked+span:before{transform:translateX(29px)}
.horizontal-bar-wrapper{display:flex;height:100vh}.horizontal-bar-right{background-color:blue;flex:1 1}
.vertical-bar-left{background-color:red;flex:1 1}
.vertical-bar-wrapper{display:flex;height:100vh}.vertical-bar-right{background-color:blue;flex:1 1}
.horizontal-stacked-bar-left{background-color:red;flex:1 1}
.horizontal-stacked-bar-wrapper{display:flex;height:100vh}.horizontal-stacked-bar-right{background-color:blue;flex:1 1}
.vertical-stacked-bar-left{background-color:red;flex:1 1}
.vertical-stacked-bar-wrapper{display:flex;height:100vh}.vertical-stacked-bar-right{background-color:blue;flex:1 1}
.area-chart-left{background-color:red;flex:1 1}
.area-chart-wrapper{display:flex;height:100vh}.area-chart-right{background-color:blue;flex:1 1}
.pie-graph-left{background-color:red;flex:1 1}
.pie-graph-wrapper{display:flex;height:100vh}.pie-graph-right{background-color:blue;flex:1 1}
.sidebar{position:fixed;top:0;left:0;height:100vh;width:320px;background-color:#fff}.sidebar__logo{display:grid;place-items:center;height:120px;font-size:1.5rem;font-weight:700;font-family:"Mochiy Pop P one",sans-serif}.sidebar__menu{position:relative}.sidebar__menu__item{display:flex;align-items:center;place-content:flex-start;padding:1rem 3rem;font-size:1.25rem;font-weight:500;color:#555;transition:color .3s ease-in-out}.sidebar__menu__item.active{color:#fff}.sidebar__menu__item__icon{margin-right:1rem}.sidebar__menu__item__icon i{font-size:1.75rem}.sidebar__menu__indicator{position:absolute;top:0;left:50%;width:calc(100% - 40px);border-radius:10px;z-index:-1;transform:translateX(-50%);transition:.3s ease-in-out}
